<template>
    <div class="detailZipkin">
        <div class="detail-back">
            <a @click="$router.push({name:'findZipkin'})"><i class="iconfont icon-fanhui"></i>返回</a>
            <span v-text="$route.meta.title + '-' + $route.query.traceId"></span>
        </div>
        <div v-loading="loading">
            <div class="header clearfix" style="width:100%;height:100%">
                <el-row  type="flex" justify="middle" class="clearfix">
                    <el-col class="header-lf" :span="2">
                        <el-tooltip class="item" effect="dark" content="调用链总耗时" placement="top">
                            <el-button>{{$t('zipkin.duration')+'：'}}</el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col class="header-rt" :span="2">
                        <el-tag class="button-new-tag" size="mini">{{detailForm.lookback}}</el-tag>
                    </el-col>
                    <el-col class="header-lf" :span="2">
                        <el-tooltip class="item" effect="dark" content="涉及的服务总数" placement="top">
                            <el-button>{{$t('zipkin.serviceNum')+'：'}}</el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col class="header-rt" :span="2">
                        <el-tag class="button-new-tag" size="mini">{{detailForm.seviceNum}}</el-tag>
                    </el-col>
                    <el-col class="header-lf" :span="2">
                        <el-tooltip class="item" effect="dark" content="调用的深度" placement="top">
                            <el-button>{{$t('zipkin.depth')+'：'}}</el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col class="header-rt" :span="2">
                        <el-tag class="button-new-tag" size="mini">{{detailForm.depth}}</el-tag>
                    </el-col>
                    <el-col class="header-lf" :span="2" style="min-width:83px;">
                        <el-tooltip class="item" effect="dark" content="调用spans的总数量" placement="top">
                            <el-button>{{$t('zipkin.spansNum')+'：'}}</el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col class="header-rt" :span="2">
                        <el-tag  class="button-new-tag" size="mini">{{detailForm.spanNum}}</el-tag>
                    </el-col>
                    <el-col class="header-js" :span="3" :offset="5" align="right">
                        <el-button @click="jsonVisible = true" icon="fa fa-download" type="primary" size="mini">JSON</el-button>
                    </el-col>
                </el-row>
                <!-- 选择框 -->
                <el-row>
                    <el-col :span="4">
                        <div class="block">
                            <el-cascader
                                size="mini"
                                placeholder="搜索"
                                :options="options"
                                @change="handleItemChange"
                                filterable
                                clearable
                            ></el-cascader>
                            {{optionValue}}
                        </div>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <div v-if="detailData.serviceCounts && detailData.serviceCounts.length > 0">
                            <span class="zipList" v-for="item in detailData.serviceCounts" :key="item.id" style="margin-right:10px">
                                {{item.name + ' x' + item.count}}
                            </span>
                        </div>
                    </el-col>
                </el-row>
            </div>
            <!-- 表格部分 -->
            <div class="main">
                <el-row v-if="detailData.timeMarkers && detailData.timeMarkers.length > 0">
                    <el-col :span="4">
                        <el-tag size="small" style="margin-left:10px">{{$t('zipkin.serviceName')}}</el-tag>
                    </el-col>
                    <el-col v-for="item in detailData.timeMarkers" :span="4" :key="item.id" align="right">
                        <el-tag size="small" v-if="item.time">{{item.time}}</el-tag>
                    </el-col>
                </el-row>
                <el-row class="main-box" v-for="(item,index) in mainData" :key="index" v-if="item.spreadStatus">
                    <div @click="checkHandle(item)" style="width:100%;height:23px">
                        <el-col :span="4" class="main-title">
                            <div class="main-title-box" :style="{'margin-left':item.depth + 'px'}">
                                <el-button @click="spreadHandle($event,item)" class="main-add" v-if="item.children === '' ? false :true">{{item.spread ? '-' :'+'}}</el-button>
                                <el-tooltip :content="item.serviceName" placement="top" >
                                    <el-button size="mini" >{{item | shortWords}}</el-button>
                                </el-tooltip>
                            </div>
                        </el-col>
                        <el-col class="main-content" :span="20" style="position:relative">
                            <el-button size="mini" :style="{'position':'absolute','width':item.width+ '%','left':item.left+ '%','background':item.spanStatus ? '#a9c9DE' : '#f56c6c'}">{{item.durationStr +' '+item.spanName}}</el-button>
                        </el-col>
                    </div>
                </el-row>
            </div>
        </div>
        <!-- detail弹出框 -->
        <el-dialog class="diglogBox" :visible.sync="dialogVisible" width="60%">
            <span slot="title" class="dialog-title">
                <span>{{diglogTitle}}</span>
            </span>
            <span class="service">{{$t('zipkin.serviceName')}} ： {{ dialogTable.serviceNames}}</span>
            <el-table
                stripe
                :data="dialogTable.annotations"
                ref="multipleTable"
                style="width: 100%;margin-top:24px">
                    <el-table-column
                        align="center"
                        :label="$t('zipkin.dateTime')">
                        <template slot-scope="props">
                            <span class="zipList">{{props.row.timestamp | timeFilter(that)}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        align="center"
                        width="150px"
                        prop="relativeTime"
                        :label="$t('zipkin.relativeTime')">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        width="200px"
                        prop="value"
                        :label="$t('zipkin.annotation')">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="endpoint"
                        :label="$t('zipkin.address')">
                    </el-table-column>
            </el-table>
            <el-table
                stripe
                :data="dialogTable.binaryAnnotations"
                ref="multipleTable"
                style="width: 100%;margin-top:24px">
                    <el-table-column
                        align="center"
                        prop="key"
                        :label="$t('zipkin.key')">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="value"
                        :label="$t('zipkin.value')">
                    </el-table-column>
            </el-table>
            <el-collapse accordion>
                <el-collapse-item :title="$t('zipkin.moreInfo')">
                    <table class="table">
                        <tbody>
                            <tr>
                                <td class="key" data-key="key">traceId</td>
                                <td class="value" data-key="value">{{detailData.traceId}}</td>
                            </tr>
                            <tr>
                                <td class="key" data-key="key">spanId</td>
                                <td class="value" data-key="value">{{dialogTable.spanId}}</td>
                            </tr>
                            <tr>
                                <td class="key" data-key="key">parentId</td>
                                <td class="value" data-key="value">{{dialogTable.parentId}}</td>
                            </tr>
                        </tbody>
                    </table>
                </el-collapse-item>
            </el-collapse>
        </el-dialog>
        <!-- JSON弹出框 -->
        <el-dialog title="Search Results" :visible.sync="jsonVisible" width="60%">
            <span slot="title" class="dialog-title">
                <span>{{$t('zipkin.searchResults')}}</span>
            </span>
            <el-button @click="copy" type="primary" size="small" class="tag-read" :data-clipboard-text="stringZip">复制</el-button>
            <el-button @click="download" type="primary" size="small">下载</el-button>
            <pre class="log-box" v-html="originalData"></pre>
        </el-dialog>
    </div>
</template>

<script type="text/ecmascript-6">
import Clipboard from 'clipboard'
import {SPAN_V1} from '@/utils/spanConverter.js'
import skew from '@/utils/skew.js'
import traceToMustache from '@/utils/traceToMustache'
import Api from '@/constants/Api.js'
export default {
    name: 'detailZipkin',
    data() {
        return {
            that: this,
            loading: false,
            options: [],
            optionValue: '',
            detailForm: {
                lookback: '',
                seviceNum: '',
                depth: '',
                spanNum: ''
            },
            originalData: {}, // 未处理数据(JSON数据)
            detailData: { // 总数据
            },
            mainData: { // 表格数据
            },
            dialogVisible: false, // detail弹出框
            diglogTitle: '', // detail弹出框标题
            dialogTable: {
                serviceName: '',
                annotations: [],
                binaryAnnotations: [],
                spanId: '',
                parentId: ''
            },
            jsonVisible: false
        }
    },
    computed: {
        stringZip() {
          return JSON.stringify(this.originalData)
        }
    },
    activated() {
      if (this.$route.query) {
        this.detailTraceId = this.$route.query.traceId
        this.getData()
      }
    },
    deactivated() {
        this.options = []
        this.optionValue = ''
        this.detailForm = {
            lookback: '',
            seviceNum: '',
            depth: '',
            spanNum: ''
        }
        this.originalData = {} // 未处理数据(JSON数据)
        this.detailData = { // 总数据
        }
        this.mainData = { // 表格数据
        }
    },
    methods: {
        // 查看详情
        checkHandle(item) {
            this.diglogTitle = item.spanName + ':' + item.durationStr
            this.dialogTable = {
                serviceNames: item.serviceNames,
                annotations: item.annotations,
                binaryAnnotations: item.binaryAnnotations,
                spanId: item.spanId,
                parentId: item.parentId
            }
            this.dialogVisible = true
        },
        // 获取数据
        getData() {
            this.options = []
            this.loading = true
            this.$axios.get(Api.zipkinDetail + this.detailTraceId).then((res) => {
                this.loading = false
                this.originalData = JSON.parse(JSON.stringify(res))
                let raw = res
                // console.log({'start': raw})
                const v1Trace = raw.map(SPAN_V1.convert)
                // console.log({'one': v1Trace})
                const mergedTrace = SPAN_V1.mergeById(v1Trace)
                // console.log({'two': mergedTrace})
                const clockSkewCorrectedTrace = skew.correctForClockSkew(mergedTrace)
                // console.log({'three': clockSkewCorrectedTrace})
                const modelview = traceToMustache(clockSkewCorrectedTrace, '')
                // console.log({'four': modelview})
                this.detailForm = {
                    lookback: modelview.duration,
                    seviceNum: modelview.services,
                    depth: modelview.depth,
                    spanNum: modelview.totalSpans
                }
                Object.keys(modelview.spans).forEach((value) => {
                    modelview.spans[value].spread = true
                    modelview.spans[value].spreadStatus = true
                    modelview.spans[value].spanStatus = !modelview.spans[value].binaryAnnotations.some((item) => {
                        let ck = false
                        if (item.key === 'exception') {
                            ck = true
                        } else {
                            if (item.key === 'RET_STATUS' && item.value !== 'S') {
                                ck = true
                            }
                            if (item.key === 'status' && item.value !== 'success') {
                                ck = true
                            }
                        }
                        return ck
                    })
                })
                this.detailData = JSON.parse(JSON.stringify(modelview))
                this.mainData = JSON.parse(JSON.stringify(modelview.spans))
                this.detailData.serviceCounts.forEach((m) => {
                    this.options.push({
                        value: m.name,
                        label: m.name
                    })
                })
            }).catch((error) => {
                this.loading = false
                this.$message.error(error.data.message || error.message || error)
            })
        },
        // 复制
        copy() {
            const _this = this
            var clipboard = new Clipboard('.tag-read')
            clipboard.on('success', e => {
                _this.$message({
                    type: 'success',
                    message: '复制成功'
                })
                // 释放内存
                clipboard.destroy()
            })
            clipboard.on('error', e => {
                // 释放内存
                clipboard.destroy()
            })
       },
       // 下载
       download() {
           if (this.detailData.traceId) {
               let name = this.detailData.traceId
               this.$axios({
                method: 'get',
                url: Api.zipkinDownload + name,
                responseType: 'blob'
            }).then(res => {
                this.commonFunc.funDownload(res, name + '.json')
            })
           }
       },
       // 搜索
        handleItemChange(val) {
            let ck = JSON.parse(JSON.stringify(this.detailData.spans))
            if (val.length <= 0) {
                this.mainData = ck
            } else {
                let arr = []
                ck.forEach((a) => {
                    val.forEach((b) => {
                        if (b === a.serviceName) {
                            arr.push(a)
                        }
                    })
                })
                this.mainData = arr
            }
        },
       // 折叠展开
       spreadHandle(ev, item) {
            ev.stopPropagation()
            let id = this.mainData.indexOf(item)
            if (item.spread === false) {
                this.$set(this.mainData[id], 'spread', true)
                for (let index = id + 1; index < this.mainData.length; index++) {
                    if (this.mainData[index].depthClass > this.mainData[id].depthClass) {
                        this.mainData[index].spread = true
                        this.mainData[index].spreadStatus = true
                    } else {
                        break
                    }
                }
            } else {
                this.$set(this.mainData[id], 'spread', false)
                for (let index = id + 1; index < this.mainData.length; index++) {
                    if (this.mainData[index].depthClass > this.mainData[id].depthClass) {
                        this.mainData[index].spreadStatus = false
                    } else {
                        break
                    }
                }
            }
       }
    },
     filters: {
        timeFilter(value, that) { // 时间转化
            let va = that.$moment(Math.trunc(value / 1000)).format('YYYY-MM-DD HH:mm:ss')
            return va
        },
        shortWords(item) { // 省略号
            let value = item.serviceName
            if (value) {
                if (value.length > 16) {
                    return value.slice(0, 15) + '...'
                } else {
                    return value
                }
            } else {
                return item.depthClass + '-' + value
            }
        }
    }
}
</script>
<style scoped lang="scss">
.detailZipkin{
    .header{
        background: #f5f5f5;
        padding: 0px 5px;
        .el-col{
            height: 40px;line-height: 40px
        }
        .header-lf{
            .el-button{
                border: none;padding:0px;
                background: #f5f5f5;
                font-weight: 600;
            }
        }
        .header-rt{
            .el-tag{
                border: none;background: #428bca;
                color: #ffffff;
            }
        }
        .header-js{
            .el-button{
                border: none;background: #428bca;
                color: #ffffff;
            }
        }
         .zipList{
            display: inline-block;
            padding: .25em .4em;
            font-size: 75%;
            font-weight: 700;
            line-height: 1;
            text-align: center;
            white-space: nowrap;
            vertical-align: baseline;
            border-radius: .25rem;
            color: #fff;
            background-color: #28a745;
        }
    }
    .main{
        font-weight: 400;
        line-height: 23px;
        color: #212529;
        text-align: left;
        font-size: 14px;
        .el-tag{
            background: #ffffff;
            border:none;
        }
        .el-button{
            border:none
        }
        .main-box{
            cursor: pointer;height: 34px;
            line-height: 34px;
            .el-button{
               color: #000000; color: #000000;
            }
            &:hover{
                background: #eee;border-color: #ccc;
            }
            .main-title{
                .main-title-box{
                    position: relative;
                    .main-add{
                        padding:7px 3px;
                        position: absolute;left:-5px;top:3px;
                        width: 14px;line-height: 14px;
                        background: none
                    }
                    .el-tooltip{
                        padding:7px 3px;margin-left: 10px;
                        background: #0084b4;color:#ffffff;
                    }
                }
            }
            .main-content{
                padding: 3px;
                .el-button{
                    background: #a9c9DE;text-align: left;
                    padding:7px 0px;
                }
                span{
                    white-space:nowrap;
                }
            }
        }
    }
    .table{
        width: 100%;
        margin-bottom: 1rem;background-color: transparent;
        border-collapse: collapse;border-spacing: 2px;
        tbody{
            display: table-row-group;
            td{
               border: 1px solid #ebeef5;text-align: center;
               line-height: 23px;padding:12px 0;
            }
        }
    }
    .el-dialog{
        .dialog-title{
            font-size: 22px;font-weight: 600;color: #000000;
        }
        .service{
            line-height: 24px;
            font-size: 16px;
            color: #303133;
        }
    }
}
</style>



